.Board {
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0 0 50px #fff, -10px 0 80px #f0f, 10px 0 80px #0ff;
}

.Board-restart {
  padding: 10px 20px;
  border-radius: 10px;
  border: none;
  margin-top: 40px;
  font-size: 18px;
  -webkit-box-shadow: 0 0 20px 2px #FFF82B;
  box-shadow: 0 0 20px 2px #FFF82B;
}

.Board-moves {
  color: #fff;
  margin-top: 15px;
}

table tr:first-child td:first-child {
  border-radius: 10px 0 0 0 !important;
}

table tr:first-child td:last-child {
  border-radius: 0 10px 0 0 !important;
}

table tr:last-child td:first-child {
  border-radius: 0 0 0 10px !important;
}

table tr:last-child td:last-child {
  border-radius: 0 0 10px 0 !important;
}

@font-face {
  font-family: neon;
  src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
}

.container {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}

.Board-title {
  display: flex;
  align-items: center;
  justify-content: center;
}

.neon-orange {
  font-family: neon;
  color: #FB4264;
  font-size: 80px;
  text-shadow: 0 0 3vw #F40A35;
  animation: neon-orange 1s ease infinite;
  -moz-animation: neon-orange 1s ease infinite;
  -webkit-animation: neon-orange 1s ease infinite;
  -o-animation: neon-orange 1s ease infinite;
}

.neon-blue {
  font-family: neon;
  color: #426DFB;
  font-size: 80px;
  text-shadow: 0 0 3vw #2356FF;
  animation: neon-blue 1s ease infinite;
  -moz-animation: neon-blue 1s ease infinite;
  -webkit-animation: neon-blue 1s ease infinite;
  -o-animation: neon-blue 1s ease infinite;
}

.winner .neon-orange,
.winner .neon-blue {
  font-size: 150px;
}

@keyframes neon-orange {

  0%,
  100% {
    text-shadow: 0 0 1vw #FA1C16, 0 0 3vw #FA1C16, 0 0 10vw #FA1C16, 0 0 10vw #FA1C16, 0 0 .4vw #FED128, .5vw .5vw .1vw #806914;
    color: #FED128;
  }

  50% {
    text-shadow: 0 0 .5vw #800E0B, 0 0 1.5vw #800E0B, 0 0 5vw #800E0B, 0 0 5vw #800E0B, 0 0 .2vw #800E0B, .5vw .5vw .1vw #40340A;
    color: #806914;
  }
}

.neon-blue {
  animation: flux 2s linear infinite;
  -moz-animation: flux 2s linear infinite;
  -webkit-animation: flux 2s linear infinite;
  -o-animation: flux 2s linear infinite;
}

@keyframes neon-blue {

  0%,
  100% {
    text-shadow: 0 0 1vw #1041FF, 0 0 3vw #1041FF, 0 0 10vw #1041FF, 0 0 10vw #1041FF, 0 0 .4vw #8BFDFE, .5vw .5vw .1vw #147280;
    color: #28D7FE;
  }

  50% {
    text-shadow: 0 0 .5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180, 0 0 .2vw #082180, .5vw .5vw .1vw #0A3940;
    color: #146C80;
  }
}

@media (max-width: 768px) {

  .neon-orange,
  .neon-blue {
    font-size: 70px;
  }

  .winner .neon-orange,
  .winner .neon-blue {
    font-size: 100px;
  }
}

@media (max-width: 480px) {

  .neon-orange,
  .neon-blue {
    font-size: 60px;
  }

  .winner .neon-orange,
  .winner .neon-blue {
    font-size: 80px;
  }
}

@media (max-width: 380px) {

  .winner .neon-orange,
  .winner .neon-blue {
    font-size: 70px;
  }
}